import React, { useState } from 'react';
import { DataTable, DataTableColumn, DataTableRowActions, Dropdown, IconSettings } from '@salesforce/design-system-react';

const Table = ({ items }) => {

    const [selection, setSelection] = useState([]);

    const onRowChange = (event, data) => {
        setSelection(data.selection);
        console.log(event, data)
    }

    const handleRowAction = (item, action) => {
        console.log(item, action);
    };

    const columns = [

        <DataTableColumn
            key="xml-name"
            label="XML NAME"
            property="XMLName"
            fixedLayout
        />,

        <DataTableColumn
            key="dir-name"
            label="DIRECTORY NAME"
            property="DirName"
            fixedLayout
        />,

        <DataTableColumn
            key="suffix"
            label="SUFFIX"
            property="Suffix"
            fixedLayout
        />,

        <DataTableColumn
            key="HasMetaFile"
            label="HAS META FILE?"
            property="HasMetaFile"
            fixedLayout
        />,

        // <DataTableColumn
        //     key="child-objects"
        //     label="Child Objects"
        //     property="ChildObjects" />,
    ];

    return (
        <div className="dataTable" style={{ overflow: 'auto' }}>
            <IconSettings iconPath="./icons" >
                <DataTable
                    items={items}
                    id="datatable"
                    fixedHeader
                    fixedLayout
                    joined
                    // stackedHorizontal
                    // selectRows="checkbox"
                    onRowChange={onRowChange}
                    selection={selection}
                >
                    <DataTableRowActions
                        options={[
                            {
                                id: 0,
                                label: 'copiar',
                                value: '1',
                            }
                        ]}
                        onAction={handleRowAction}
                        dropdown={<Dropdown length="1" />}
                    />
                    {columns}
                </DataTable>
            </IconSettings>
        </div>
    );

}

export default Table;